import React, { Component } from 'react';
import Increase from '../Increase/Increase';
import './Header.css';

export default class Header extends Component {
  state = {
  };

  render() {
    const {
      currentScore,
      increaseNum,
      restartAction,
      maxScore,
      level
    } = this.props;
    return (
      <div className="header-warper">
        <span className="title2048">
          2048
        </span>
        <span className="game-level">
          
          {level}
          
        </span>
        <div className="header-right">
          <div className="score">
            <span className="">
              
              <br />
              {maxScore}
            </span>
          </div>
          <div className="score">
            <span className="">
              
              <br />
              {currentScore}
              <Increase
                increaseNum={increaseNum}
              />
            </span>
          </div>
        </div>
        <div className="btn-wraper">
          <button
            type="button"
            className="restart-btn"
            onClick={restartAction}
          >
            
          </button>
          <div className="readme-wrap">
            <div className="readme"></div>
            <div className="readme">PCW A S D     </div>
            <div className="readme">:0~3,~</div>
          </div>
        </div>
      </div>
    );
  }
}



